
.position_support{
  padding-top: 40px;
}
.section_support{
  background:var(--support-ukraine-modal-menu-background);
  max-width: 335px;
  height:352px;
  border-radius: 16px;
  margin: 0 auto;
}
.slider_line{
  top: 0;
  height:188px;
  position:relative;
  margin: auto 0;
  transition: all ease 1s;
}

.slider {
overflow: hidden;
}
.ol_support{
  list-style-type: none;
  counter-reset: item;
  color:rgba(255, 255, 255, 1);
  font-weight:400;
  font-size:14px;
  line-height:1.28;
  letter-spacing:-4%;
  margin-top:40px;
 
}
.list::before {
  content: '0'counter(item);
  counter-increment: item;

 }
.list{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  max-width: 159px;
  height: 32px;
  gap: 16px;
}
.header_support{
line-height:1.14;
letter-spacing:-4%;
padding-top: 24px;
color:rgba(255, 255, 255, 1);
display: flex;
gap:12px;
padding-left: 40px;
}

.scroll{
position: absolute;
padding-left: 152px;
padding-bottom: 20px;
margin-top: 16px;
cursor: pointer;
}
.scroll_svg{
  background: var(  --support-ukraine-font-color);
  border-radius: 50%;
}
/* ================ Tablet ====================== */
@media screen and (min-width: 768px) {
  .section_support{
      width: 335px;
      height:472px;
  }
  .slider_line{
      height:292px;   
  }
  .ol_support{
      list-style-type: none;
      counter-reset: item;
      color:rgba(255, 255, 255, 1);
      font-weight:400;
      font-size:14px;
      line-height:1.28;
      letter-spacing:-4%;
      margin-top:40px;
}
.scroll{
  padding-left: 149px;
  padding-bottom: 14px;
  margin-top: 32px;
  }
}
/* ================ Desctop ====================== */
@media screen and (min-width: 1440px) {
.position_support{
      padding-top: 86px;
  }
}

