.switch {
  height: 0;
  width: 0;
  visibility: hidden;
}

.switch-label {
  cursor: pointer;
  text-indent: -9999px;
  width: 40px;
  height: 20px;
  background: var(--theme-switcher-bg);
  border-radius: 100px;
  display: block;
  position: relative;
}

.switch-label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: #fff;
  border-radius: 90px;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switch:checked + .switch-label:after {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.switch-label:active:after {
  width: 26px;
}

